.main-dashboard-component {
  margin-bottom: 60px;
  h1 {
    color: var(--Text, #211b3a);
    font-family: Urbanist;
    font-size: 24px;
    font-style: normal;
    font-weight: 600;
    line-height: 28px;
    /* 116.667% */
  }
}

.tableheading {
  h5 {
    color: #211b3a;
    font-family: Urbanist;
    font-size: 20px;
    font-style: normal;
    font-weight: 600;
    line-height: 28px; /* 140% */
  }
  input.form-control {
    max-width: 300px;
    border-radius: 8px;
    padding: 8px 12px;
    overflow: hidden;
    color: #868686;
    font-family: Urbanist;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 20px;
    letter-spacing: 0.25px;
  }
}
.table th {
  font-family: Urbanist;
  color: #492fb2;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: 16px; /* 114.286% */
  letter-spacing: 0.07px;
}

.table td {
  color: #333;
  font-family: Urbanist;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: 150%; /* 21px */
  letter-spacing: 0.25px;
}
.custom-table {
  border: 1px solid #e5e7eb;
  border-radius: 8px;
  thead th {
    background-color: #eee;
    padding: 12px;
  }
}

.badge {
color: #333;
font-family: Urbanist;
font-size: 14px;
font-style: normal;
font-weight: 500;
line-height: 150%; /* 21px */
letter-spacing: 0.25px;
}
.dropdown-menu {
  border-radius: 8px;
  border: 1px solid #c6c6c6;
  background: #fff;
  cursor: pointer;
  padding: 8px 0px;
  .dropdown-item {
    padding: 8px 16px;
    display: flex;
    align-items: center;
    gap: 8px;
    font-family: Urbanist;
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
  }
  .blue {
    color: #492fb2;
  }
  .black {
    color: #211b3a;
  }
  .red {
    color: #b7131a;
  }
}
.btn-check:checked+.btn, .btn.active, .btn.show, .btn:first-child:active, :not(.btn-check)+.btn:active {
    border-color: transparent;
}